<template>
  <div style="height: 100% ;background-color: #DADADA80;">
    <div v-for="(item,index) in tabs" :key="index" class="div-content">
      <div class="cardWrap" :style="[{backgroundColor:itemColor(index)}]"
              @click="onItemSelect(index)" @mouseenter="mouseListener(index,true)"
              @mouseleave="mouseListener(index,false)">
        <img :src="item.img"/>
        <span :style="{color:current == index?'#FFFFFF':null}">{{ item.name }}</span>
      </div>
      <div v-if="index == tabs.length -2 || (index+1)%4==0" style="width: 100% ;height:1px ;background-color: grey"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "HomeTab",
  props:{
    current:null,
  },
  data() {
    return {
      normalColor: "#DADADA80",
      normalColorWithMouse: "#5A667E60",
      selectColor: "#5A667E",
      tabs: [
        {name: "报表中心", img: require("../../assets/tabs/dashboard.png"), "enter": false},
        {name: "收货管理", img: require("../../assets/tabs/inbound.png"), "enter": false},
        {name: "发货管理", img: require("../../assets/tabs/outbound.png"), "enter": false},
        {name: "库存管理", img: require("../../assets/tabs/inventory.png"), "enter": false},
        {name: "财务中心", img: require("../../assets/tabs/finanic.png"), "enter": false},
        {name: "商品管理", img: require("../../assets/tabs/goods_list.png"), "enter": false},
        {name: "仓库设置", img: require("../../assets/tabs/storehouse.png"), "enter": false},
        {name: "用户管理", img: require("../../assets/tabs/user_manager.png"), "enter": false},
        {name: "上传中心", img: require("../../assets/tabs/upload_center.png"), "enter": false},
        {name: "电商店铺", img: require("../../assets/tabs/e_shop.png"), "enter": false},
        {name: "基本设置", img: require("../../assets/tabs/base_setting.png"), "enter": false},
      ],
    }
  },
  emits:["itemSelect"],
  methods: {
    itemColor(index) {
      if (this.current == index) return this.selectColor
      if (this.tabs[index].enter) return this.normalColorWithMouse
      return this.normalColor
    },
    onItemSelect(index) {
      if (this.current != index) {
        this.$emit('itemSelect',index,this.tabs[index].name)
      }
    },
    mouseListener(index, isEnter) {
      this.tabs[index].enter = isEnter
    }
  }
}
</script>

<style scoped>
img {
  width: 35px;
  height: 35px;
  fill: antiquewhite;
  scale: inherit;
}

span {
  margin-left: 20px;
  margin-right: 10px;
  font-size: 16px;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.58);
}

.cardWrap {
  display: flex;
  justify-content: flex-start;
  padding: 10px 20px;
  align-items: center;
  border-width: 0;
}

.div-content {
  width: max-content;
  background-color: beige;
}
</style>