      <img v-on:click="onUserClick" src="../../assets/tabs/user_center.png" style="margin:15px"/>
    </div>
  </div>
</template>
<script>
export default {
  name: "HomeHeader",
  emits: ['tab-control','user'],
  computed:{
    userName(){
      return this.$store.state.token;
    }
  },
  methods: {
    onUserClick() {
      if (!this.userName){
        this.$router.push('/login')
      } else {
        console.log("!!!!!!!!1")
        this.$emit('user')
      }
    },
  },
}
</script>

