<template>
  <div id="app" class="layout">
    <home-header @tab-control="tabControl" @user="goToSetting"/>
    <div class="body">
      <home-tab v-if="isShowTab" @item-select="itemSelect" :current="currentPageIndex"/>
      <keep-alive>
        <router-view/>
      </keep-alive>
    </div>
  </div>
</template>

<script>
import {defineAsyncComponent, ref} from "vue";
import HomeTab from "@/components/home/HomeTab";
import HomeHeader from "@/components/home/HomeHeader";
import Cookies from "js-cookie";
import {UPDATE_USER} from "@/router/mutations";

const componentsMap = ref(new Map())
componentsMap.value.set("login", defineAsyncComponent(() => import("./LoginAndRegisterPage")))
const pageName = ref('login')
export default {
  components: {
    HomeTab, HomeHeader,
  },
  name: 'HomePage',
  data() {
    return {
      pageName: pageName,
      componentsMap: componentsMap,
      isShowTab: true,
      title: HomeTab.data().tabs[0].name,
      currentPageIndex: 0,
      mainContent: ""
    }
  },
  created() {
    let token = Cookies.get('token');
    this.$store.commit(UPDATE_USER, token)
  },

  mounted() {
    this.currentPageIndex = this.$router.currentRoute.value.name
  },

  methods: {
    tabControl() {
      this.isShowTab = !this.isShowTab
      this.$router.push({name: "10"})
    },
    itemSelect(index, title) {
      this.currentPageIndex = index
      this.title = title
      this.$router.push({name: "" + this.currentPageIndex})
    },
    goToSetting() {
      this.itemSelect(HomeTab.data().tabs.length - 1)
    }
  }
}


</script>

<style scoped>
.layout {
  width: 100%;
  height: max-content;
  position: absolute;
}

.body {
  width: 100%;
  height: 100%;
  background-color: #f6f5f3;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  margin-top: 60px;
}


</style>